import React, { useRef, useState } from "react";
import styled from "styled-components";
import Navbar from "../components/Navbar";

const PageContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 80px;
    background: linear-gradient(to right, #1e3c72, #2a5298);
    min-height: 100vh;
    color: white;
`;

const Section = styled.section`
    width: 100%;
    max-width: 900px;
    margin: 50px auto;
    padding: 60px 30px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;

    &:hover {
        transform: translateY(-5px);
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
    }
`;

const Title = styled.h1`
    font-size: 2.2em;
    font-family: "Poppins", sans-serif;
    color: #f4a261;
    margin-bottom: 20px;
`;

const Form = styled.form`
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: auto;
    padding: 20px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
`;

const Input = styled.input`
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
`;

const TextArea = styled.textarea`
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  min-height: 120px;
`;

const SubmitButton = styled.button`
  background: #f4a261;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  
  &:hover {
    background: #e76f51;
    transform: scale(1.05);
  }
`;

const ContactPage = () => {
    const homeRef = useRef(null);
    const aboutRef = useRef(null);
    const projectsRef = useRef(null);
    const contactRef = useRef(null);

    const [formData, setFormData] = useState({
        name: "",
        email: "",
        message: "",
    });

    const [formStatus, setFormStatus] = useState("");

    const scrollToSection = (section) => {
        if (section === "home") homeRef.current.scrollIntoView({ behavior: "smooth" });
        if (section === "about") aboutRef.current.scrollIntoView({ behavior: "smooth" });
        if (section === "projects") projectsRef.current.scrollIntoView({ behavior: "smooth" });
        if (section === "contact") contactRef.current.scrollIntoView({ behavior: "smooth" });
    };

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setFormStatus("Submitting...");

        // Sim√ºle edilmi≈ü form g√∂nderimi
        setTimeout(() => {
            setFormStatus("Thank you for your message! I will get back to you soon.");
            setFormData({ name: "", email: "", message: "" });
        }, 2000);
    };

    return (
        <>
            <Navbar scrollToSection={scrollToSection} />
            <PageContainer>

                <Section ref={homeRef} id="home">
                    <Title>Welcome to My Portfolio!</Title>
                    <p>I am a software engineer specializing in React, JavaScript, and Gatsby.</p>
                </Section>

                <Section ref={aboutRef} id="about">
                    <Title>About Me</Title>
                    <p>Hi, I'm Metahan. I'm passionate about web development and technology.</p>
                </Section>

                <Section ref={projectsRef} id="projects">
                    <Title>Projects</Title>
                    <ul>
                        <li>üöÄ Project 1 - React Dashboard</li>
                        <li>üì± Project 2 - Mobile App with React Native</li>
                        <li>üåç Project 3 - Personal Website</li>
                    </ul>
                </Section>

                <Section ref={contactRef} id="contact">
                    <Title>Contact Me</Title>
                    <p>You can reach me at: <strong>contact@example.com</strong></p>
                    <Form onSubmit={handleSubmit}>
                        <label>Name:</label>
                        <Input
                            type="text"
                            name="name"
                            value={formData.name}
                            onChange={handleChange}
                            required
                        />

                        <label>Email:</label>
                        <Input
                            type="email"
                            name="email"
                            value={formData.email}
                            onChange={handleChange}
                            required
                        />

                        <label>Message:</label>
                        <TextArea
                            name="message"
                            value={formData.message}
                            onChange={handleChange}
                            required
                        />

                        <SubmitButton type="submit">Send Message</SubmitButton>
                        {formStatus && <p>{formStatus}</p>}
                    </Form>
                </Section>

            </PageContainer>
        </>
    );
};

export default ContactPage;